<template>
  <v-container class="fill-height" fluid>
    <v-row align="center">
      <v-btn block x-large color="green" v-on:click="start()" dark
        >Start Script</v-btn
      >
    </v-row>
    <v-spacer></v-spacer>
    <v-row>
      <v-btn block x-large color="red" v-on:click="stop()" dark
        >Stop Script</v-btn
      >
    </v-row>
    <v-spacer></v-spacer>
  </v-container>
</template>

<script>
// import api from "../services/api";
import axios from "axios";

export default {
  data: () => ({
    info: "",
  }),
  methods: {
    start() {
      // Start Script
      // axios
      //   .post("http://localhost:5000/_start_script")
      //   .then((response) => (this.info = response));
      axios
        .get("http://localhost:5000")
        .then((response) => {
          console.log(response.data);
          alert(response.data)
        });
      console.log("START");
    },
    stop() {
      // Stop Script
      axios
        .post("http://localhost:5000/_stop_script")
        .then((response) => (this.info = response));
      console.log("STOP");
    },
  },
};
</script>
