  <v-card>
    <v-tabs v-model="tab" background-color="primary" dark>
      <v-tab v-for="item in items" :key="item.tab">{{ item.tab }}</v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item v-for="item in items" :key="item.tab">
        <v-container class="fill-height" fluid>
          <v-row align="center">
            <v-col cols="12" sm="12">
              <v-data-table
                :headers="headers"
                :items="item.content"
                sort-by="name"
                class="elevation-1"
              >
                <template v-slot:top>
                  <v-toolbar flat color="white">
                    <v-toolbar-title
                      >My {{ item.tab }} Settings</v-toolbar-title
                    >
                    <v-divider class="mx-4" inset vertical></v-divider>
                    <v-spacer></v-spacer>
                    <v-dialog v-model="dialog" max-width="500px">
                      <v-card>
                        <v-card-title>
                          <span class="headline">{{ formTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                          <v-container>
                            <v-row>
                              <v-col cols="12" sm="4" md="4">
                                <v-text-field
                                  v-model="editedItem.name"
                                  label="Settings name"
                                  readonly
                                ></v-text-field>
                              </v-col>
                              <v-col cols="12" sm="8" md="8">
                                <v-text-field
                                  v-model="editedItem.value"
                                  label="Value"
                                ></v-text-field>
                              </v-col>
                            </v-row>
                          </v-container>
                        </v-card-text>

                        <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn color="blue darken-1" text @click="close"
                            >Cancel</v-btn
                          >
                          <v-btn color="blue darken-1" text @click="save"
                            >Save</v-btn
                          >
                        </v-card-actions>
                      </v-card>
                    </v-dialog>
                  </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                  <v-icon small class="mr-2" @click="editItem(item)"
                    >mdi-pencil</v-icon
                  >
                </template>
                <template v-slot:no-data>
                  <v-btn color="primary" @click="initialize">Reset</v-btn>
                </template>
              </v-data-table>
            </v-col>
          </v-row>
        </v-container>
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>

<script>
import api from "../services/api";

export default {
  data: () => ({
    tab: null,
    items: [
      {
        tab: "Software",
        content: [],
        url: "/_software_settings_file",
        file: "Application/VisionSystem/settings/software_settings.json",
      },
      {
        tab: "Dashboard",
        content: [],
        url: "/_dashboard_settings_file",
        file: "Application/VisionSystem/settings/dashboard_settings.json",
      },
      {
        tab: "Camera",
        content: [],
        url: "/_camera_settings_file",
        file: "Application/VisionSystem/settings/Default_Camera_Settings.json",
      },
    ],
    dialog: false,
    headers: [
      { text: "Settings Name", value: "name" },
      { text: "Value", value: "value" },
      { text: "Actions", value: "actions", sortable: false },
    ],
    settings: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      value: 0,
    },
    defaultItem: {
      name: "",
      value: 0,
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New Item" : "Edit Item";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      for (let index = 0; index < this.items.length; index++) {
        const x = this.items[index];
        api
          .get(x.url)
          .then((response) => {
            let temp = [];
            for (let i = 0; i < Object.keys(response.data).length; i++) {
              let obj = {};
              obj.name = Object.keys(response.data)[i];
              obj.value = Object.values(response.data)[i];
              temp.push(obj);
            }
            x.content = temp;
            this.settings[index] = temp;
          })
          .catch(function(error) {
            console.log(error);
          });
      }
    },
    editItem(item) {
      this.editedIndex = this.settings[this.tab].findIndex(
        (element) => element.name === item.name
      );
      this.editedItem = Object.assign({}, this.settings[this.tab][this.editedIndex]);
      this.dialog = true;
    },
    // deleteItem(item) {
    //   const found = this.settings[this.tab].find(element => element.name === item.name);
    //   const index = this.settings[this.tab].indexOf(item);
    //   confirm("Are you sure you want to delete this item?") &&
    //     this.settings[this.tab].splice(index, 1);
    // },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },
    save() {
      if (this.editedIndex > -1) {
        Object.assign(
          this.settings[this.tab][this.editedIndex],
          this.editedItem
        );
      } else {
        this.settings[this.tab].push(this.editedItem);
      }
      var settingsObj = {};
      this.settings[this.tab].forEach((element) => {
        settingsObj[element.name] = element.value;
      });

      let temp = {};
      this.settings[this.tab].forEach((item) => {
        temp[item.name] = item.value;
      });
      api
        .post('/_save_settings_file' + "?file=" + this.items[this.tab].file,
          temp
        )
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
      console.log("STOP");
      this.close();
    },
  },
};
</script>
