<template>
  <v-app>
    <v-app-bar
      :clipped-left="$vuetify.breakpoint.lgAndUp"
      app
      color="amber accent-2"
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon>
      <v-toolbar-title style="width: 300px" class="ml-0 pl-4">
        <span class="hidden-sm-and-down">Hose Vision System</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <span style="font-weight: bold"
        >Pending Classification : {{ imagesToClassify }}</span
      >
      <v-spacer></v-spacer>
      <span class="hidden-sm-and-down"
        >Current Hose Series: {{ current_hose_series }}</span
      >
      <v-btn icon @click="dialog = !dialog">
        <v-icon>mdi-pencil</v-icon>
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer
      v-model="drawer"
      :clipped="$vuetify.breakpoint.lgAndUp"
      app
    >
      <v-list dense>
        <template v-for="item in items">
          <v-row v-if="item.heading" :key="item.heading" align="center">
            <v-col cols="6">
              <v-subheader v-if="item.heading">{{ item.heading }}</v-subheader>
            </v-col>
          </v-row>
          <v-list-item v-else :key="item.text" :to="item.link">
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>
            <v-list-item-content>
              <v-list-item-title>{{ item.text }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-dialog v-model="dialog" width="500px">
      <v-card>
        <v-card-title class="grey darken-2">Set Hose Series</v-card-title>
        <v-container>
          <v-row align="center" justify="center" class="mx-2">
            <v-col cols="6">
              <span>Choose the Hose Series:</span>
            </v-col>
            <v-col cols="6">
              <v-overflow-btn
                :items="dropdown_font"
                v-model="selected_current_hose_series"
                target="#dropdown-example"
              ></v-overflow-btn>
            </v-col>
          </v-row>
        </v-container>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn text color="primary" @click="dialog = false">Cancel</v-btn>
          <v-btn text @click="save()">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-content>
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
import api from "./services/api";

var hose_series = localStorage.getItem("hose_series") || "836-8";

export default {
  name: "App",
  data: () => ({
    dialog: false,
    drawer: null,
    imagesToClassify: 0,
    dropdown_font: [
      "436-12",
      "471-16",
      "487-6",
      "487-8",
      "SS23CG-8",
      "482-4",
      "836-8",
    ],
    current_hose_series: localStorage.getItem("hose_series") || "836-8",
    selected_current_hose_series:
      localStorage.getItem("hose_series") || "836-8",
    items: [
      {
        icon: "mdi-contacts",
        link: "/classifier/" + hose_series,
        text: "Image Classifier",
      },
      { icon: "mdi-cog", link: "/script", text: "Script" },
      { icon: "mdi-cog", link: "/settings", text: "Settings" },
    ],
  }),
  async beforeCreate() {
    // this.current_hose_series = "836-8";
  },
  async created() {
    // this.current_hose_series = "836-8";
    api
      .get("/_hose_series")
      .then((response) => {
        this.current_hose_series = response.data;
        console.log(response);
      })
      .catch(function(error) {
        console.log(error);
      });

    api
      .get("/_picture_number")
      .then((response) => {
        this.imagesToClassify = response.data;
        console.log(response);
      })
      .catch(function(error) {
        console.log(error);
      });
  },
  methods: {
    save() {
      this.current_hose_series = this.selected_current_hose_series;
      localStorage.setItem("hose_series", this.current_hose_series);
      api
        .post("/_hose_series", this.current_hose_series)
        .then(function(response) {
          console.log(response);
        })
        .catch(function(error) {
          console.log(error);
        });
      this.dialog = false;
    },
  },
};
</script>
